<template>
    <div class="other">
        <template v-if="showPage.contact">
            <div class="map-wrapper">
                <Bmap :big='true'/>
            </div>
            <div class="contact-way">
                <div class="left">
                    <div class="sq">
                        <img :src="require('common/img/qr_code01.jpg')" alt="">
                        <p>更多资讯扫一扫</p>
                        <p>加入我们的公众号</p>
                    </div>
                    <div class="sq">
                        <img :src="require('common/img/qr_code01.jpg')" alt="">
                        <p>更多资讯扫一扫</p>
                        <p>加入我们的公众号</p>
                    </div>
                </div>
                <div class="right">
                    <p class="title">预订服务热线: 023-66450999</p>
                    <p>咨询电话：023-66450999</p>
                    <p>传真：</p>
                    <p>地址：重庆市南川区东泉镇正街102号</p>
                    <p>微信公众号：重庆秀泉映月温泉花园酒店</p>
                </div>
            </div>    
        </template>
        <div class="contact-wrapper" v-if="showPage.message">
            <div class="slogan">
                <h1 class="en">
                    <p>ONLINE </p>
                    <p>MESSAGE</p>
                </h1>
                <h1 class="cn">
                    在线留言
                </h1>
                <p class="desc">
                    您的每一个建议<br />都是我们服务的方向
                </p>
            </div>
            <div class="message-board">
                <div class="item">
                    <span>姓名:</span>
                    <input type="text" placeholder="请输入您的姓名">
                </div>
                <div class="item">
                    <span>电话:</span>
                    <input type="text" placeholder="请输入您的手机号码">
                </div>
                <div class="item">
                    <span>建议:</span>
                    <input type="text" placeholder="请提出你的建议">
                </div>
                <div class="item">
                    <span>描述:</span>
                    <textarea type="text" placeholder="请尽可能的描述您的建议" rows="3">
                    </textarea>
                </div>
                <div class="btn">
                    <Ibutton content='提 交' :big='true'/>
                </div>
            </div>
        </div>
        <div class="show-wrapper" v-if="showPage.viewer">
            <iframe :src="frameSrc" frameborder="0" width="100%" height="800"></iframe>
        </div>
    </div>
</template>
<script>
import Bmap from '_c/bmap'
import Ibutton from '_c/ibutton'
export default {
    data() {
        return {
            showPage: {
                contact: false,
                viewer: false,
                message: false,
            },
            frameSrc: 'http://api.map.baidu.com/pano/?ac=1&iec=0&showCatalog=1&indoorSceneSwitchControl=0&h=294.969558076627&p=0&pid=0902920000140806171258340IN&ak=Clo6QgwDUUHpwF7wX6Mw31IaLR0m2Dbw',
            noflashSrc: 'https://map.baidu.com/#panoid=73afe301cf90b365a4c756bd&panotype=inter&heading=266&pitch=0&l=21&tn=B_NORMAL_MAP&sc=0&newmap=1&shareurl=1&pid=0902920000140806163815390IN&iid=73afe301cf90b365a4c756bd&psp=%7B%22PanoIndoorPoiModule%22%3A%7B%22uid%22%3A%22279e368eae3bb29dd7851f3e%22%7D%7D'
        }
    },
    mounted() {
        let { cateid, cateindex } = this.$route.query
        this.selectShowPage(cateid, cateindex)
    },
    methods: {
        selectShowPage (cateid, cateindex) {
            for (let key in this.showPage) {
                this.showPage[key] = false
            }
            if (cateindex == 0) {
                // this.$set(this.showPage, 'contact', true)
                this.showPage.contact = true
            } else if (cateindex == 1) {
                this.showPage.viewer = true
                this.initMap()
            } else if (cateindex == 2) {
                this.showPage.message = true
            } else {
            }
        },
        initMap () {
            let swf;
            if (navigator.userAgent.indexOf("MSIE") > 0) {
                try {
                    swf = new ActiveXObject("ShockwaveFlash.ShockwaveFlash");
                    // console.log('已安装flash插件');
                }
                catch (e) {
                    // console.log('没有安装flash插件');
                    this.frameSrc = this.noflashSrc
                }
            }
            if (navigator.userAgent.indexOf("Firefox") > 0 || navigator.userAgent.indexOf("Chrome") > 0) {
                swf = navigator.plugins["Shockwave Flash"];
                if (swf) {
                    // console.log('已安装flash插件')
                } else {
                    // console.log('没有安装flash插件');
                    this.frameSrc = this.noflashSrc
                }
            }
        }
    },
    components: {
        Bmap, Ibutton
    }
}
</script>
<style lang="less" scoped>
.other{
    min-height: 600px;
    width: 100%;
    .map-wrapper{
        height: 600px;
        width: 100%;
        margin: 30px 0;
    }
    .contact-way{
        display: flex;
        padding: 30px;
        margin-bottom: 30px;
        background-color: #f5f5f5;
        .left{
            display: flex;
            justify-content: space-around;
            width: 50%;
            border-right: 1px solid #ccc;
            .sq{
                width: 150px;
                img{
                    width: 100%;
                }
                p{
                    font-size: 14px;
                }
            }
        }
        .right{
            width: 50%;
            padding: 20px 30px;
            box-sizing: border-box;
            text-align: left;
            p{
                font-size: 14px;
                line-height: 30px;
                padding-left: 20px;
            }
            .title{
                color: #e0c073;
                font-size: 25px;
                padding-bottom: 10px;
            }
        }
    }
    .contact-wrapper{
        height: 100%;
        width: 100%;
        .slogan{
            height: 600px;width: 40%;
            padding-left: 150px;
            float: left;
            box-sizing: border-box;
            text-align: left;
            display: flex;
            justify-content: flex-start;
            flex-direction: column;
            .en{
                color: #e0c073;
                font-size: 35px;
                margin-top: 60px;
                line-height: 50px;
                p{
                    padding: 10px 0;
                }
            }
            .cn{
                padding: 50px 0 50px 0;
                color: #e0c073;
                font-size: 30px;
            }
            .desc{
                line-height: 35px;
                font-size: 20px;
                color: #999;
            }
        }
        .message-board{
            height: 600px;width: 55%;
            float: right;
            padding: 50px 0 0 50px;
            box-sizing: border-box;
            text-align: left;
            .item{
                height: 55px;
                margin-bottom: 35px;
                box-sizing: border-box;
                span{
                    vertical-align: top;
                    line-height: 55px;
                    font-size: 25px;
                    padding-right: 50px;
                }
                input{
                    height: 55px;
                    width: 70%;
                    padding: 0 19px;
                    border-radius: 8px;
                    box-sizing: border-box;
                    border: none;
                    font-size: 20px;
                    outline-color: #e0c073;
                    background-color: #f1f1f1;
                }
                textarea{
                    vertical-align: top;
                    width: 70%;
                    padding: 0 19px;
                    line-height: 50px;
                    border-radius: 8px;
                    box-sizing: border-box;
                    border: none;
                    font-size: 20px;
                    outline-color: #e0c073;
                    background-color: #f1f1f1;
                    resize: none;
                    &::-webkit-scrollbar{
                        width: 1px;
                    }
                }
            }
            .btn{
                margin-top: 120px;
                text-align: center;
            }
        }
    }
    .show-wrapper{
        height: 100%;
        width: 100%;
        min-height: 600px;
        padding: 30px 0;
    }
}
</style>



