<template>
    <div class="bmap" id="bmap">
        
    </div>    
</template>
<script>
export default {
    props: {
        big: {
            type: Boolean,
            default: false
        }
    },
    mounted() {
        this.initMap()
    },
    methods: {
        gocenter() {
            this.map.panTo(new BMap.Point(106.87161, 29.456955))
        },
        initMap () {
            // 百度地图API功能
            this.map = new BMap.Map("bmap");    // 创建Map实例
            let point = new BMap.Point(106.87161,29.456955)
            this.map.centerAndZoom(point, 18);  // 初始化地图,设置中心点坐标和地图级别
            //添加地图类型控件
            this.map.addControl(new BMap.MapTypeControl({
                mapTypes:[
                    BMAP_NORMAL_MAP,
                    BMAP_HYBRID_MAP
                ]}));	  
            this.map.setCurrentCity("重庆");          // 设置地图显示的城市 此项是必须设置的
            this.map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放

            var top_left_control = new BMap.ScaleControl({anchor: BMAP_ANCHOR_TOP_LEFT});// 左上角，添加比例尺
            var top_left_navigation = new BMap.NavigationControl();  //左上角，添加默认缩放平移控件
            var top_right_navigation = new BMap.NavigationControl({anchor: BMAP_ANCHOR_TOP_RIGHT, type: BMAP_NAVIGATION_CONTROL_SMALL}); //右上角，仅包含平移和缩放按钮
            // 缩略图
            if (this.big) {
                var overView = new BMap.OverviewMapControl();
                var overViewOpen = new BMap.OverviewMapControl({isOpen:true, anchor: BMAP_ANCHOR_BOTTOM_RIGHT});
                this.map.addControl(overView)
                this.map.addControl(overViewOpen)
                // 信息框
                this.setMesWindow(point)
            }
            /*缩放控件type有四种类型:
            BMAP_NAVIGATION_CONTROL_SMALL：仅包含平移和缩放按钮；BMAP_NAVIGATION_CONTROL_PAN:仅包含平移按钮；BMAP_NAVIGATION_CONTROL_ZOOM：仅包含缩放按钮*/
            
            // 添加控件和比例尺
            // this.map.addControl(top_left_control);        
            // this.map.addControl(top_left_navigation);     
            // this.map.addControl(top_right_navigation);    
            if (!this.big) {
                var marker = new BMap.Marker(point);  // 创建标注
                this.map.addOverlay(marker);               // 将标注添加到地图中
                marker.setAnimation(BMAP_ANIMATION_BOUNCE); //跳动的动画
            }
            

            // 添加带有定位的导航控件
            var navigationControl = new BMap.NavigationControl({
                // 靠左上角位置
                anchor: BMAP_ANCHOR_TOP_LEFT,
                // LARGE类型
                type: BMAP_NAVIGATION_CONTROL_LARGE,
                // 启用显示定位
                enableGeolocation: true
            });
            this.map.addControl(navigationControl);
            // 添加定位控件
            var geolocationControl = new BMap.GeolocationControl();
            geolocationControl.addEventListener("locationSuccess", function(e){
                // 定位成功事件
                var address = '';
                address += e.addressComponent.province;
                address += e.addressComponent.city;
                address += e.addressComponent.district;
                address += e.addressComponent.street;
                address += e.addressComponent.streetNumber;
                // alert("当前定位地址为：" + address);
            });
            geolocationControl.addEventListener("locationError",function(e){
                // 定位失败事件
                alert(e.message);
            });
            this.map.addControl(geolocationControl);
        },
        setMesWindow (point) {
             var content = `<div style="margin:0;line-height:20px;padding:2px;">
             <img src="https://p.ssl.qhimg.com/sdm/242_150_/t01dd750f555287da41.jpg" alt="" style="float:right;zoom:1;overflow:hidden;width:100px;height:100px;margin-left:3px;"/>
             地址：重庆市南川区东泉镇正街102号<br/>电话：(023)66450999<br/>简介：世界珍稀命名级氟泉锶泉-原汤天然温泉-秀泉映月温泉。
             </div>`;

            //创建检索信息窗口对象
            var searchInfoWindow = null;
            searchInfoWindow = new BMapLib.SearchInfoWindow(this.map, content, {
                    title  : "秀泉映月温泉花园酒店",      //标题
                    width  : 290,             //宽度
                    height : 105,              //高度
                    panel  : "panel",         //检索结果面板
                    enableAutoPan : true,     //自动平移
                    searchTypes   :[
                        BMAPLIB_TAB_SEARCH,   //周边检索
                        BMAPLIB_TAB_TO_HERE,  //到这里去
                        BMAPLIB_TAB_FROM_HERE //从这里出发
                    ]
                });
            var marker = new BMap.Marker(point); //创建marker对象
            marker.enableDragging(); //marker可拖拽
            searchInfoWindow.open(marker);
            this.map.addOverlay(marker); //在地图中添加marker
        }
    }
}
</script>
<style lang="less" scoped>
.bmap{
    height: 100%;
    width: 100%;
}
</style>



